<template>
    <div>
        <div class="wrap">
            <div class="inner_wrap">
                <div>Web 환경</div>
                <div class="btn_wrap">
                    <button
                        class="btn_dev"
                        :class="{
                            active: domainMode === ''
                        }"
                        @click="domainMode = ''"
                    >
                        LIVE
                    </button>
                    <button
                        class="btn_dev"
                        :class="{
                            active: domainMode === 'stg'
                        }"
                        @click="domainMode = 'stg'"
                    >
                        STG
                    </button>
                    <button
                        class="btn_dev"
                        :class="{
                            active: domainMode === 'dev'
                        }"
                        @click="domainMode = 'dev'"
                    >
                        DEV
                    </button>
                </div>
                <p v-if="domainMode === ''">
                    * LIVE 경로는 CROS우회설정된 브라우저에서만 정상 동작 합니다.
                </p>
                <div>H/E 환경</div>
                <div class="btn_wrap">
                    <button
                        class="btn_dev"
                        :class="{
                            active: serverMode === 'LIVE'
                        }"
                        @click="serverMode = 'LIVE'"
                    >
                        LIVE
                    </button>
                    <button
                        class="btn_dev"
                        :class="{
                            active: serverMode === 'STG'
                        }"
                        @click="serverMode = 'STG'"
                    >
                        STG
                    </button>
                </div>
                <div>Protocol</div>
                <div class="btn_wrap">
                    <button
                        class="btn_dev"
                        :class="{
                            active: protocol === 'https'
                        }"
                        @click="protocol = 'https'"
                    >
                        HTTPS
                    </button>
                    <button
                        class="btn_dev"
                        :class="{
                            active: protocol === 'http'
                        }"
                        @click="protocol = 'http'"
                    >
                        HTTP
                    </button>
                </div>
                <div class="btn_wrap margin_top">Web Version</div>
                <div class="btn_wrap">
                    <input v-model="webVersion" class="btn_input" />
                </div>
                <div class="btn_wrap margin_top">셋탑</div>
                <div class="btn_wrap">
                    <button
                        v-for="name in devList"
                        :key="name"
                        class="btn_dev active"
                        @click="openTestBrowser(name)"
                    >
                        {{ name }}
                    </button>
                </div>
                <div class="btn_wrap margin_top">Custom</div>
                <div class="btn_wrap">settop id: <input v-model="stbId" class="btn_input" /></div>
                <div class="btn_wrap">mac: <input v-model="mac" class="btn_input" /></div>
                <div class="btn_wrap">
                    settop model: <input v-model="stbModel" class="btn_input" />
                </div>
                <div class="btn_wrap">
                    <button class="btn_dev active" @click="openCustomBrowser()">열기</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { settopList } from './devSettop';

export default {
    name: 'App',
    data() {
        return {
            domainMode: 'stg',
            serverMode: 'LIVE',
            protocol: 'https',
            webVersion: 'v535',
            stbId: '',
            mac: '',
            stbModel: ''
        };
    },
    computed: {
        devList() {
            return Object.keys(settopList);
        }
    },
    components: {},
    methods: {
        openTestBrowser(name) {
            const settop = settopList[name];
            window.open(
                `${this.protocol}://agw${
                    this.domainMode ? `-${this.domainMode}` : ''
                }.sk-iptv.com:${this.protocol === 'https' ? '8443' : '8080'}/ui5web/${
                    this.webVersion
                }/?stbId=${encodeURIComponent(settop.stbId)}&mac=${encodeURIComponent(
                    settop.mac
                )}&stbModel=${encodeURIComponent(settop.stbModel)}&env=${this.serverMode}`
            );
        },
        openCustomBrowser() {
            window.open(
                `${this.protocol}://agw${
                    this.domainMode ? `-${this.domainMode}` : ''
                }.sk-iptv.com:${this.protocol === 'https' ? '8443' : '8080'}/ui5web/${
                    this.webVersion
                }/?stbId=${encodeURIComponent(this.stbId)}&mac=${encodeURIComponent(
                    this.mac
                )}&stbModel=${encodeURIComponent(this.stbModel)}&env=${this.serverMode}`
            );
        }
    }
};
</script>

<style>
.tool_toggle {
    color: #f8585b;
    font-size: 100px;
    position: absolute;
    padding: 10px;
    cursor: pointer;
}

.wrap {
    font-size: 20px;
}

.inner_wrap {
    padding: 20px;
}

.btn_dev {
    background-color: #aaa;
    border: none;
    color: #fff;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 20px;
    margin-right: 15px;
    cursor: pointer;
    border-radius: 10px;
    margin-top: 10px;
}

.btn_dev.active {
    background-color: #f8585b;
}

.btn_input {
    font-size: 20px;
    border: 1px solid black;
}

.margin_top {
    margin-top: 10px;
}

.switch {
    position: relative;
    display: inline-block;
    width: 100px;
    height: 54px;
    margin-left: 15px;
    margin-top: 10px;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

.slider:before {
    position: absolute;
    content: '';
    height: 45px;
    width: 50px;
    left: 6px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

input:checked + .slider {
    background-color: #f8585b;
}

input:focus + .slider {
    box-shadow: 0 0 1px #f8585b;
}

input:checked + .slider:before {
    -webkit-transform: translateX(39px);
    -ms-transform: translateX(39px);
    transform: translateX(39px);
}

.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}
</style>
